import Link from "next/link";
import { connect } from "react-redux";
import { setContext } from "../store.js";

function Context({ setContext }) {
  return (
    <div className="container">
      <div className="columns">
        <div className="column col-4 col-mr-auto">col-4 col-mr-auto</div>
        <div className="column col-2">col-2</div>
      </div>
    </div>
  );
}
function mapDispatchToProps(dispatch) {
  return { setContext: context => dispatch(setContext(context)) };
}

export default connect(
  null,
  mapDispatchToProps
)(Context);



